import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import TenantComplains from './EstateManagerPages/TenantComplains'
import useJwt from '@src/auth/jwt/useJwt'
import PropertyBadge from '../../assets/images/dashboardBadges/property.svg'
import branchBadge from '../../assets/images/dashboardBadges/branch.svg'
import contractorBadge from '../../assets/images/dashboardBadges/contractor.svg'
import landlordBadge from '../../assets/images/dashboardBadges/landlord.svg'
import tenantBadge from '../../assets/images/dashboardBadges/tenant.svg'
import Media from 'reactstrap/lib/Media'
import CardText from 'reactstrap/lib/CardText'
import { useHistory } from 'react-router-dom'

const Dash = () => {
    const history = useHistory()
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const Statuses = [
        { count: 0, id: 2, name: 'Completed' },
        { count: 0, id: 3, name: 'Pending' },
        { count: 0, id: 4, name: 'Cancelled' },
        { count: 0, id: 7, name: 'Accepted' },
        { count: 0, id: 10, name: 'Quotation Pending' },
        { count: 0, id: 11, name: 'Quotation Approved' },
        { count: 0, id: 12, name: 'Need Approval' },
        { count: 0, id: 13, name: 'Finished' }
    ]
    const usersIds = [
        { count: 0, id: 3, name: 'Landlord', badge: landlordBadge },
        { count: 0, id: 4, name: 'Tenant', badge: tenantBadge },
        { count: 0, id: 5, name: 'Contractor', badge: contractorBadge },
        { count: 0, id: 6, name: 'Branches', badge: branchBadge }
    ]
    const badges = {
        3: landlordBadge,
        4: tenantBadge,
        5: contractorBadge,
        6: branchBadge
    }
    const links = {
        3: `/${userRole}/LandlordList`,
        4: `/${userRole}/TenantList`,
        5: `/${userRole}/ContractorList`,
        6: `/${userRole}/MyBranches`
    }
    const [UsersEA, setUsersEA] = useState([
        {
            count: 0,
            name: 'Contractor'
        },
        {
            count: 0,
            name: 'Landlord'
        },
        {
            count: 0,
            name: 'Tenant'
        }
    ])
    const [complains, setcomplains] = useState([
        { count: 0, id: 3, name: 'Pending' },
        { count: 0, id: 12, name: 'Need Approval' },
        { count: 0, id: 7, name: 'Accepted' },
        { count: 0, id: 10, name: 'Quotation Pending' },
        { count: 0, id: 11, name: 'Quotation Approved' },
        { count: 0, id: 13, name: 'Finished' },
        { count: 0, id: 2, name: 'Completed' },
        { count: 0, id: 4, name: 'Cancelled' }
    ])
    const [property, setproperties] = useState([
        {
            count: 0,
            name: 'Total'
        },
        {
            count: 0,
            name: 'Occupied'
        },
        {
            count: 0,
            name: 'Unoccupied'
        }
    ])
    useEffect(() => {
        useJwt.dashboardSummary().then(res => {
            console.log("dashboardSummary", res.data.payload)
            const { complain_counter, property, user_counter } = res.data.payload
            const complaincounter = null
            const usercounter = null
            const restCOmplains = !complain_counter ? [] : Statuses.filter(c => false === complain_counter.some(cc => cc.status_id === c.id))
            const restUsers = !user_counter ? [] : usersIds.filter(u => false === user_counter.some(uu => uu.user_type === u.id))
            console.log(restUsers)
            setUsersEA(!user_counter ? UsersEA : [...user_counter.map(y => { return { link: links[y.user_type], badge: badges[y.user_type], count: y.number_of_user, name: y.user_type_name } }), ...restUsers])
            setcomplains(!complain_counter ? complains : [...complain_counter.map(x => { return { count: x.number_of_complain, name: x.status_name } }), ...restCOmplains]
            )
            setproperties(!property ? properties : [
                {
                    count: property.occupied_property.length + property.unoccupied_property.length,
                    name: 'Total'
                },
                {
                    count: property.occupied_property.length,
                    name: "Occupied"
                },
                {
                    count: property.unoccupied_property.length,
                    name: 'Unoccupied'
                }
            ])
        }).catch(error => {
            console.log("dashboardSummary", error)
            Error(error)
        })
    }, [])
    return (
        <>
            <Row className='match-height'>
                <Col md='4'>
                    <Row>
                        {/* <Col md='12'>

                        </Col> */}
                        {
                            property.map((x, i) => <Col md={`${i === 0 ? '12' : '6'}`}>
                                <Card key={i} onClick={e => history.push(`/${userRole}/MyProperty`)} className='cursor-pointer'>
                                    {i === 0 && <CardHeader className='text-primary pl-1 m-0'>
                                        <CardTitle tag='h4' className='text-primary p-0 m-0'>
                                            <img src={PropertyBadge} alt="logo" className="img-fluid" />&nbsp;&nbsp;Property
                                        </CardTitle>
                                    </CardHeader>}
                                    <div className={`text-center ${i === 0 ? 'pt-0' : 'pt-1'}`}>
                                        <h3><b>{x.count}</b></h3>
                                        <p className='text-muted'>{x.name}</p>
                                    </div>
                                </Card>
                            </Col>)
                        }
                    </Row>
                </Col>
                <Col md='8'>
                    <Card>
                        <Row>
                            {UsersEA.map((x, i) => <Col key={i} md='6' className='px-5 py-3 cursor-pointer' onClick={e => history.push(x.link)}>
                                {userRole === 'EstateManager' && i === 3 ? '' : <Media>
                                    <img src={x.badge} className='img-fluid mr-1' />
                                    <Media className='my-auto' body>
                                        <h4 className='font-weight-bolder mb-0'>{x.count}</h4>
                                        <CardText className='font-small-2 mb-0'>{x.name}</CardText>
                                    </Media>
                                </Media>}
                                {/* <div className="text-center pt-1">
                                    <h3><b>{x.count}</b></h3>
                                    <p className='text-muted'>{x.name}</p>
                                </div> */}
                            </Col>)
                            }
                        </Row>
                    </Card>
                </Col>
            </Row >
            {/* <Row>
                {UsersEA.map((x, i) => <Col key={i} sm={userRole === 'EstateManager' ? "4" : "3"}>
                    <Card>

                        <div className="text-center pt-1">
                            <h3><b>{x.count}</b></h3>
                            <p className='text-muted'>{x.name}</p>
                        </div>
                    </Card>
                </Col>)
                }
            </Row>
            <Card>
                <CardHeader>
                    <CardTitle tag='h4'>Property</CardTitle>
                </CardHeader>
                <CardBody className="d-flex flex-row justify-content-around flex-wrap">
                    {
                        property.map((x, i) => <div key={i} className="text-center pt-1 mx-1">
                            <h3><b>{x.count}</b></h3>
                            <p className='text-muted'>{x.name}</p>
                        </div>)
                    }
                </CardBody>
            </Card> */}
            {
                userRole !== 'Estatemanager' && <><Card>
                    <CardHeader>
                        <CardTitle tag='h4'>Complains Overview</CardTitle>
                    </CardHeader>
                    <CardBody className="d-flex flex-row justify-content-around flex-wrap">
                        {
                            complains.map((x, i) => <div key={i} className="text-center pt-1 mx-1">
                                <h3><b>{x.count}</b></h3>
                                <p className='text-muted'>{x.name}</p>
                            </div>)
                        }
                    </CardBody>
                </Card>
                    <TenantComplains /></>
            }
        </>
    )
}

export default Dash